.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.formik {
  display: flex;
  flex-direction: column;
}

.errorDiv {
  color: red;
  font-size: 20px;
}

input {
  margin-bottom: 5px;
  background: transparent;
  border: 1px green solid;
}

button {
  background: transparent;
  color: #fff;
  cursor: pointer;
}

input, button {
  border-radius: 5px;
}
